import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { LayoutComponent } from './layout.component';
import { AuthGuard } from '../../guards/auth.guard';
import { PermissionGuard } from '../../guards/permission.guard';

const routes: Routes = [
  
  {
    path: '',
    component: LayoutComponent,
    children: [
      { path: '', redirectTo: 'dashboard', pathMatch: 'full' },
      {

        path: 'dashboard',
        loadChildren: () =>
          import('../dashboard/dashboard-module').then(
            (m) => m.DashboardModule
          ),
        canActivate: [PermissionGuard],
        data: { permission: 'dashboard.index' }
      },
      {
        path: 'users',
        loadChildren: () =>
          import('../users/users-module').then((m) => m.UsersModule),
        canActivate: [PermissionGuard],
        data: { permission: 'user.index' }
      },
      {
        path: 'roles',
        loadChildren: () =>
          import('../roles/roles.module').then((m) => m.RolesModule),
        canActivate: [PermissionGuard],
        data: { permission: 'admin.index' }
      },
      {
        path: 'employees',
        loadChildren: () =>
          import('../employees/employees-module').then(
            (m) => m.EmployeesModule
          ),
        canActivate: [PermissionGuard],
        data: { permission: 'employee.index' }
      },
      {
        path: 'profile',
        loadComponent: () =>
          import('../profile/profile.component').then(
            (m) => m.ProfileComponent
          ),
      },
      {
        path: 'departments',
        loadChildren: () =>
          import('../departments/departments.module').then(
            (m) => m.DepartmentsModule
          ),
        canActivate: [PermissionGuard],
        data: { permission: 'department.index' }
      },
      {
        path: 'tasks',
        loadChildren: () =>
          import('../tasks/tasks-module').then((m) => m.TasksModule),
        canActivate: [PermissionGuard],
        data: { permission: 'task.index' }
      },
      {
        path: 'partners',
        loadChildren: () =>
          import('../partners/partners-module').then((m) => m.PartnersModule),
        canActivate: [PermissionGuard],
        data: { permission: 'partners.index' }
      },
      {
        path: 'visits',
        loadChildren: () =>
          import('../visits/visits-module').then((m) => m.VisitsModule),
        canActivate: [PermissionGuard],
        data: { permission: 'visits.index' }
      },
      {
        path: 'travel',
        loadChildren: () =>
          import('../travel/travel.module').then((m) => m.TravelModule),
        canActivate: [PermissionGuard],
        data: { permission: 'travel.index' }
      },
      {

        path: 'meetings',
        loadChildren: () =>
          import('../meetings/meetings.module').then((m) => m.MeetingsModule),
        canActivate: [PermissionGuard],
        data: { permission: 'meeting.index' }
      },
      {
        path: 'my-calendar',
        loadChildren: () =>
          import('../calendar/calendar.module').then((m) => m.CalendarModule),
        canActivate: [PermissionGuard],
        data: { permission: 'calendar.index' }
      },
      {
        path: 'calendar',
        loadChildren: () =>
          import('../calendar/system-calendar/system-calendar.module').then((m) => m.SystemCalendarModule),
        canActivate: [PermissionGuard],
        data: { permission: 'calendar.events.get' }
      },
      {
        path: 'letters',
        loadChildren: () =>
          import('../letters/letters.module').then((m) => m.LettersModule),
        canActivate: [PermissionGuard],
        data: { permission: 'letter.index' }
      },
      {
        path: 'documents',
        loadChildren: () =>
          import('../documents/documents.module').then((m) => m.DocumentsModule),
        canActivate: [PermissionGuard],
        data: { permission: 'documents.index' }
      },
      {
        path: 'workflow',
        loadChildren: () =>
          import('../workflow/workflow.module').then((m) => m.WorkflowModule),
        canActivate: [PermissionGuard],
        data: { permission: 'workFlow.index' }
      },
      // {
      //   path: '',
      //   redirectTo: 'dashboard',
      //   pathMatch: 'full',
      // },
    ],
    canActivate: [AuthGuard]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class LayoutRoutingModule {}
