import { Component, OnInit, OnDestroy, signal } from '@angular/core';
import { RouterOutlet } from '@angular/router';
import { LangService } from '../../services/lang.service';
import { AuthService } from '../../services/auth.service';
import { Router } from '@angular/router';
import { TranslationService } from '../../services/translation.service';

@Component({
  selector: 'app-layout',
  templateUrl: './layout.component.html',
  styleUrls: ['./layout.component.css'],
  standalone: false
})
export class LayoutComponent implements OnInit, OnDestroy {
  isRTL = signal(false);
  user:any
  sidebarCollapsed = signal(false);
  currentLang = signal<'ar' | 'en'>('en');
  showLogoutConfirm = signal(false);
  showNotificationModal = signal(false);
  isMobile = signal(false);
  mobileSidebarOpen = signal(false);

  constructor(
    private langService: LangService,
    private authService: AuthService,
    private router: Router,
    private translationService: TranslationService
  ) {}

  ngOnInit(): void {
    // Subscribe to language changes
    this.langService.currentLang$.subscribe((lang: 'ar' | 'en') => {
      this.currentLang.set(lang);
      this.isRTL.set(lang === 'ar');
      const stored=localStorage.getItem('user');
      this.user=stored?JSON.parse(stored):null;
    });
    
    // Check initial screen size
    this.checkScreenSize();
    
    // Add resize listener
    window.addEventListener('resize', () => this.checkScreenSize());
    
    console.log('Layout component initialized');
  }

  onSidebarCollapsed(collapsed: boolean): void {
    this.sidebarCollapsed.set(collapsed);
  }

  setLanguage(lang: 'ar' | 'en'): void {
    this.langService.setLanguage(lang);
    console.log('Language changed to:', lang);
  }

  onLogoutConfirmRequested(): void {
    this.showLogoutConfirm.set(true);
  }

  closeLogoutConfirm(): void {
    this.showLogoutConfirm.set(false);
  }

  // Notification modal methods
  openNotificationModal(): void {
    this.showNotificationModal.set(true);
  }

  closeNotificationModal(): void {
    this.showNotificationModal.set(false);
  }

  onSendNotification(notification: any): void {
    // Handle notification sending logic here
    console.log('Notification sent:', notification);
    this.closeNotificationModal();
  }

  logout(): void {
    this.showLogoutConfirm.set(false);
    this.authService.logout().subscribe({
      next: () => {
        this.router.navigate(['/auth/login']);
      },
      error: (error) => {
        console.error('Logout error:', error);
        this.router.navigate(['/auth/login']);
      }
    });
  }

  translate(key: string): string {
    return this.translationService.translate(key);
  }

  private checkScreenSize(): void {
    const isMobile = window.innerWidth <= 768;
    this.isMobile.set(isMobile);
    
    // Close mobile sidebar when switching to desktop
    if (!isMobile && this.mobileSidebarOpen()) {
      this.mobileSidebarOpen.set(false);
    }
  }

  toggleMobileSidebar(): void {
    this.mobileSidebarOpen.update(open => !open);
  }

  closeMobileSidebar(): void {
    this.mobileSidebarOpen.set(false);
  }

  ngOnDestroy(): void {
    // Clean up resize listener
    window.removeEventListener('resize', () => this.checkScreenSize());
  }
}
